import { useState,useEffect } from "react";
import { useParams } from "react-router-dom";
import { Nav } from 'react-bootstrap'
import { useDispatch } from "react-redux";
import { addCart } from "./../store/shoppingBasketSlice";
import { useLike } from "../hooks/like";

function ProductDetailPage(props){
  let dispatch = useDispatch() //Redux 등록된 데이터 변경함수 사용하기
  let [tabNum, setTabNum] = useState(0)
  let {id} = useParams();
  let findProduct = props.shoes.find(function(x){
    return x.id == id
  })

  let [like, addLike] = useLike()
  //최근본 상품저장
  let watched = JSON.parse(localStorage.getItem('watched'))
  let newWatch = new Set(watched)
  newWatch.add(id)
  localStorage.setItem('watched', JSON.stringify(Array.from(newWatch)))

  let imgUrl = "https://codingapple1.github.io/shop/shoes" +(Number(findProduct.id)+1)+".jpg"
  
    return(
      <div className="container">
        <div className="row">
          <div className="col-md-6">
            <img src={ imgUrl } width="100%" />
          </div>
          <div className="col-md-6">
            <h4 className="pt-5">{findProduct.title}</h4>
            <p>{findProduct.content}</p>
            <p>{findProduct.price}</p>
            <p>{like}<span onClick={()=>{ addLike((a)=>{return(a+1)}) }}>❤</span></p>
           
            <button className="btn btn-danger" onClick={()=>{ dispatch(addCart(findProduct)) }}>주문하기</button> 
    
          </div>
        </div>
        <div>
          <Nav variant="tabs"  defaultActiveKey="link0">
            <Nav.Item>
              <Nav.Link eventKey="link0" onClick={()=>setTabNum(0)}>버튼0</Nav.Link>
            </Nav.Item>
            <Nav.Item>
              <Nav.Link eventKey="link1" onClick={()=>setTabNum(1)}>버튼1</Nav.Link>
            </Nav.Item>
            <Nav.Item>
              <Nav.Link eventKey="link2" onClick={()=>setTabNum(2)}>버튼2</Nav.Link>
            </Nav.Item>
          </Nav>
          <TabContent tabNum={ tabNum } ></TabContent>
        </div>
    </div> 
    )
  }

  function TabContent({tabNum}){
    let [fade , setFade] = useState("")
    useEffect(()=>{
      let timer = setTimeout(()=>{setFade("end")},100)
      return()=>{
        clearTimeout(timer)
        setFade("")}
    },[tabNum])
    return (
      <div className={"start "+fade}>
     {[<div>content 0</div>,<div>content 1</div>,<div>content 2</div>][tabNum]}
     </div>
    )
  }
  
  export default ProductDetailPage;