
import { useState } from "react";
  /*setLike(like+1)
  동일한 소스 setLike((a)=>{return(a+1)})  , setLike(a=> a+1) 축약한 소스 
  a 기존스테이트 값*/
  //useState를 쓰면 함수 이름은 use~로 시작해야함
  export function useLike(){
    let [like, setLike] = useState(0)
    function addLike(){
        setLike(a=> a+1)
    }
    return [like, addLike]
  }
